import React, { Component } from 'react'
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';

import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';

export const CREATE_ITEM_MUTATION = gql`
    mutation CREATE_ITEM_MUTATION(
        title: String!
        description: String!
        image: String
        largeImage: String
        price: Int!
    ){
        CreateItem(
            title: 'Cool'
            description: $description
            image: $image
            largeImage: $largeImage
            price: $price
        ){
            id
        }
    }
`

class CreateItem extends Component {
    state = {
        title: '',
        description: '',
        image: '',
        largeImage: '',
        price: 0,
    }

    handleChange = (e) => {
        const { name, type, value } = e.target;
        const val = type === 'number' ? parseFloat(value) : value;
        this.setState({
            [name]: val
        })
    }

    render() {
        const {
            title,
            description,
            image,
            largeImage,
            price,
        } = this.state;
        return (
            <Form onSubmit={e => {
                e.preventDefault();
                console.log(this.state)
            }}>
                <fieldset>
                    <label htmlFor="title">
                        Title
                        <input type="text" name="title" id="title" placeholder="Title" value={title} onChange={this.handleChange} required />
                    </label>
                    <label htmlFor="price">
                        Price
                        <input type="number" name="price" id="price" placeholder="Price" value={price} onChange={this.handleChange} required />
                    </label>
                    <label htmlFor="description">
                        Description
                        <textarea name="description" id="description" placeholder="Enter a description" value={description} onChange={this.handleChange} required />
                    </label>
                    <button type="submit">Submit</button>
                </fieldset>
            </Form>
        )
    }
}

export default CreateItem;
